import { DiagLogFunction } from '@opentelemetry/api';

export interface Logger {
    error: DiagLogFunction;
    warn: DiagLogFunction;
    info: DiagLogFunction;
    debug: DiagLogFunction;
    [key: string]: any;
}

export default interface AspectoClientOptions {
    disableAspecto?: boolean;

    env?: string;
    aspectoAuth?: string;

    /**
    @deprecated use use {@link serviceName}
    */
    packageName?: string;
    serviceName?: string;

    /**
    @deprecated use use {@link serviceVersion}
    */
    packageVersion?: string;
    serviceVersion?: string;

    /**
    if set, telemetry data will additionally be exported to this zipkin backend
    */
    customZipkinEndpoint?: string;

    /**
    @deprecated use use {@link otlpCollectorEndpoint}
    */
    otCollectorEndpoint?: string;

    /**
    full url, including protocol and target. example: https://collector.aspecto.io/v1/traces
    */
    otlpCollectorEndpoint?: string; //
    ciReport?: boolean;
    baseUrl?: string;

    writeSystemLogs?: boolean;
    samplingRatio?: number;
    requireConfigForTraces?: boolean;
    collectPayloads?: boolean;

    exportBatchSize?: number;

    /**
    @deprecated use use {@link exportBatchTimeoutMs}
    */
    exportBatchTimeout?: number;
    exportBatchTimeoutMs?: number;

    extractB3Context?: boolean;
    injectB3ContextSingleHeader?: boolean;
    injectB3ContextMultiHeader?: boolean;

    // instrumentation customization

    /**
    default - true.
    should be true when the service receiveMessages from SQS which is subscribed to SNS and subscription configured with "Raw message delivery": Disabled.
    setting to false is a bit more performant as it turns off JSON parse on message payload
    */
    sqsExtractContextPropagationFromPayload?: boolean;

    /**
    @deprecated no need to set this to start live stream tracing session
    */
    local?: boolean;
    logger?: Logger;

    /**
    set this if you don't want to print the live steam traces message to the console
    */
    disableLiveStreamTraces?: boolean;
}
